import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

@Component({
  selector: 'app-bealanding',
  templateUrl: './bealanding.component.html',
  styleUrls: ['./bealanding.component.scss']
})
export class BealandingComponent implements OnInit {

  public listTrans: Array<any>
  public idperiode: String
  public isempty: boolean = true
  constructor(private toastr: ToastrService,
    private route: ActivatedRoute,
    private router: Router,
    private http: HttpClient,) {

  }

  ngOnInit(): void {
    this.idperiode = this.route.snapshot.params['idperiode'];

    this.http.get("https://pelaporanpliiapi.azurewebsites.net/api/TransaksiLelang", this.generateHeader()).subscribe((result: any) => {

      this.listTrans = result.data
      console.log(result)
      if (this.listTrans.length > 0) {
        this.isempty = false

      }
    }, error => {


    });
  }
  onKirim(idtrans) {
    if (confirm("Apakah anda yakin ingin mengirim data ke PPPK?")) {
        console.log(idtrans);
        this.http.put("https://pelaporanpliiapi.azurewebsites.net/api/TransaksiLelang/Kirim?id="+idtrans, this.generateHeader()).subscribe(data => {
            console.log("post ressult ", data);
            this.toastr.info("Jadwal Terkirim ke P2PK")
            this.router.navigate(['/jadwaldetail/' + this.idperiode]);

        }, error => {
            this.toastr.error("Tidak dapat mengirim data, Periksa kembali data Anda");
            console.log(error);
        });
    }
}

  generateHeader() {
    let token = localStorage.getItem('token').replace('"', '')
    console.log(token)
    let options = { headers: new HttpHeaders({ Authorization: "Bearer " + token }) };
    return options;
  }

}
