import { Component } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Periode } from '@/type/periode';

@Component({
    selector: 'app-jadwaldetail',
    templateUrl: './jadwaldetail.component.html',
    styleUrls: ['./jadwaldetail.component.scss']
})
export class JadwalDetailComponent {
    public listJadwal: Array<any>
    public idperiode: String
    public isempty: boolean = true

    constructor(private toastr: ToastrService,
        private route: ActivatedRoute,
        private router: Router,
        private http: HttpClient,) {

    }
    ngOnInit(): void {
        this.idperiode = this.route.snapshot.params['idperiode'];

        this.http.get("https://pelaporanpliiapi.azurewebsites.net/api/JadwalLelang/AllPerPeriode/" + this.idperiode, this.generateHeader(1)).subscribe((result: any) => {

            this.listJadwal = result.data 
            console.log(result)
            if (this.listJadwal.length > 0) {
                this.isempty = false

            }
        }, error => {


        });

    }
    generateHeader1() {
        let token = localStorage.getItem('token').replace('"', '')
        console.log(token)
        let options = { headers: new HttpHeaders({ Authorization: "Bearer " + token }) };
        console.log(options)
        return options;
    }
    generateHeader(idjadwal) {
        let token = localStorage.getItem('token').replace('"', '')
        console.log(token)
        let options = { headers: new HttpHeaders({ Authorization: "Bearer " + token }), params: new HttpParams().set('id', idjadwal) };
        console.log(options)
        return options;
    }
    onKirim(idjadwal) {
        if (confirm("Apakah anda yakin ingin mengirim data ke PPPK?")) {
            console.log(idjadwal);
            const body = { id: idjadwal }
            this.http.put("https://pelaporanpliiapi.azurewebsites.net/api/JadwalLelang/Kirim?id="+idjadwal, this.generateHeader1()).subscribe(data => {
                console.log("post ressult ", data);
                this.toastr.info("Jadwal Terkirim ke P2PK")
                this.router.navigate(['/jadwaldetail/' + this.idperiode]);

            }, error => {
                this.toastr.error("Tidak dapat mengirim data, Periksa kembali data Anda");
                console.log(error);
            });
        }
    }

}
