import { Component, OnInit } from '@angular/core';

import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { UntypedFormGroup, UntypedFormControl, Validators } from '@angular/forms';
import { v4 as uuidv4 } from 'uuid'


@Component({
    selector: 'app-jadwallist',
    templateUrl: './jadwallist.component.html',
    styleUrls: ['./jadwallist.component.scss']
})
export class JadwallistComponent {

    public listJadwal: Array<any>

    constructor(private toastr: ToastrService,
        private route: ActivatedRoute,
        private router: Router,
        private http: HttpClient,) {

    }
    ngOnInit(): void {

        this.http.get("https://pelaporanpliiapi.azurewebsites.net/api/PeriodePelaporan", this.generateHeader()).subscribe((result: any) => {

            this.listJadwal = result.data
            console.log(result)
        }, error => {


        });


    }
    generateHeader() {
        let token = localStorage.getItem('token').replace('"', '')
        let options = { headers: new HttpHeaders({ Authorization: "Bearer " + token }) };
        return options;
    }


}
