import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient, HttpHeaders ,HttpParams} from '@angular/common/http';

@Component({
  selector: 'app-beadetail',
  templateUrl: './beadetail.component.html',
  styleUrls: ['./beadetail.component.scss']
})
export class BeadetailComponent implements OnInit {

  public listTrans: Array<any> 
  public idperiode : String
  public isempty : boolean = true
  constructor(private toastr: ToastrService,
    private route: ActivatedRoute,
    private router: Router, 
    private http: HttpClient,) {

}

ngOnInit(): void {
  this.idperiode = this.route.snapshot.params['idperiode'];
  
  this.http.get("https://pelaporanpliiapi.azurewebsites.net/api/LaporanPenyetoranBeaLelang", this.generateHeader()).subscribe((result: any) => {

    this.listTrans = result.data
    console.log(result)
    if (this.listTrans.length > 0) {
        this.isempty = false

    }
}, error => {


});
}
generateHeader() {
  let token = localStorage.getItem('token').replace('"', '')
  console.log(token)
  let options = { headers: new HttpHeaders({ Authorization: "Bearer " + token })};
  return options;
}

}
